.g-big-feature {
  display: flex;
  flex: 0 1 auto;
  flex-basis: auto;
  flex-direction: row;
  position: relative;
  max-width: 100%;
}
.g-big-feature * {
  box-sizing: border-box;
}
@media (max-width: 820px) {
  .g-big-feature {
    display: block;
    flex-direction: column;
  }
}
@media (max-width: 820px) {
  .g-big-feature a {
    margin: 0;
  }
}
@media (max-width: 820px) and (hover: none) {
  .g-big-feature a {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  .g-big-feature a:active {
    border: none;
    box-shadow: none;
  }
}
@media (max-width: 820px) {
  @media not all and (hover: none), all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .g-big-feature a:hover, .g-big-feature a:focus {
      border: none;
      box-shadow: none;
    }
  }
}
.g-big-feature ul, .g-big-feature li {
  display: block;
  list-style: none;
  margin: 0 !important;
  padding: 0 !important;
}
.g-big-feature ul {
  display: flex;
  flex: 0 1 auto;
  flex-basis: auto;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.g-big-feature li {
  width: 100%;
}
.g-big-feature a {
  border: 0 solid rgba(0, 0, 0, 0);
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  display: block;
  margin: 0;
  position: relative;
  z-index: 1;
  transition: all 0.1s;
}
@media (hover: none) {
  body:not(.wp-admin) .g-big-feature a {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  body:not(.wp-admin) .g-big-feature a:active {
    z-index: 2;
    transform: scale(1.03);
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.3), 0 0 1em rgba(0, 0, 0, 0.4);
  }
}
@media not all and (hover: none), all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  body:not(.wp-admin) .g-big-feature a:hover, body:not(.wp-admin) .g-big-feature a:focus {
    z-index: 2;
    transform: scale(1.03);
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.3), 0 0 1em rgba(0, 0, 0, 0.4);
  }
}

.g-big-feature img {
  display: block;
  width: 100%;
  height: auto;
}
@media (hover: none) {
  body:not(.wp-admin) .g-big-feature .g-bf-cluster a {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  body:not(.wp-admin) .g-big-feature .g-bf-cluster a:active {
    transform: scale(1.05);
  }
}
@media not all and (hover: none), all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  body:not(.wp-admin) .g-big-feature .g-bf-cluster a:hover, body:not(.wp-admin) .g-big-feature .g-bf-cluster a:focus {
    transform: scale(1.05);
  }
}

.g-big-feature .g-bf-container {
  flex-direction: column;
  flex-grow: 1;
  width: calc(100% / 3);
}
.g-big-feature .g-bf-container:nth-child(even) {
  flex-direction: column-reverse;
}
@media (max-width: 820px) {
  .g-big-feature .g-bf-container {
    width: 100%;
  }
  .g-big-feature .g-bf-container, .g-big-feature .g-bf-container:nth-child(even) {
    flex-direction: row;
  }
  .g-big-feature .g-bf-container:nth-child(even) {
    flex-direction: row-reverse;
  }
  .g-big-feature .g-bf-container li {
    flex-grow: 1;
    width: 100%;
  }
  .g-big-feature .g-bf-container .g-bf-cluster {
    width: 50%;
  }
  .g-big-feature .g-bf-container .g-bf-cluster ul {
    flex-direction: column;
  }
  .g-big-feature .g-bf-container .g-bf-cluster li {
    flex-grow: 0;
    flex-shrink: 1;
  }
}